/* 全局样式 */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #001529; /* 暗黑背景色 */
  color: #c7c7c7; /* 文字颜色 */
  overflow-x: hidden; /* 隐藏横向滚动条 */
  overflow-y: hidden; /* 隐藏横向滚动条 */
}

/* 布局样式 */
.layout-header {
  background-color: #001529;
  padding: 0px;
  text-align: center;
}

.layout-footer {
  background-color: #001529;
  padding: 0px;
  text-align: center;
}

.layout-sider {
  background-color: #001529;
  padding: 0px;
  height: 90vh; /* 设置侧边栏高度填满视窗高度 */
  overflow-y: auto; /* 如果内容过多，启用滚动条 */
  position: fixed; /* 固定侧边栏 */
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 77777;
  transition: width 0.2s; /* 平滑宽度变化的过渡效果 */
}

.layout-content {
  background-color: #e6f7ff;
  padding: 0px;
  margin-left: calc(100% - 95%); /* 动态计算左边距，确保内容不会超出视口宽度 */
  transition: margin-left 0.2s; /* 平滑边距变化的过渡效果 */
  flex-grow: 1; /* 弹性布局，内容区会填满除了侧边栏外的可用空间 */

}

.layout-body {
  display: flex; /* 使用Flexbox进行布局 */
  flex-direction: column; /* 设置主轴方向为垂直方向 */
}

.layout-main {
  display: flex; /* 内部元素使用Flexbox布局 */
  flex-grow: 1; /* 占据除了header和footer外的所有可用空间 */
  width: 100%; /* 使用100%而非80%，确保不超过视口宽度 */
  right: 20px;
  margin-top: 10px; /* 根据header的高度调整 */
}

.layout-footer, .layout-header, .layout-content {
  flex-shrink: 0; /* 防止header和footer在Flexbox布局中被压缩 */
}

/* 适应侧边栏折叠的样式 */
.layout-sider-collapsed {
  background-color: #001529;
  width: 0px; /* 折叠后的宽度 */
}

.layout-content-collapsed {
  margin-left: 0px; /* 侧边栏折叠时Content的左边距 */
}

.filter-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 90%;
  z-index: 1000; /* 确保filter在其他内容上方 */
  background-color: #001529; /* 根据需要调整背景颜色 */
}
